import React, {useState, useEffect} from "react";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistAdd";
import CardEditMenu from './CardEditMenu.js'
import SearchBox from '../../../../../components/SearchBox/index';
import {useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import {base_url, $http} from "config";
import {Modal, ModalHeader} from 'reactstrap';
import CloseIcon from '@material-ui/icons/Close';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import IntlMessages from 'util/IntlMessages';
import SweetAlert from 'react-bootstrap-sweetalert'

const columnData = [
  {id: "id", align: false, disablePadding: true, label: "ID"},
  {id: "site_name", align: true, disablePadding: false, label: "Site Name"},
  {id: "city", align: true, disablePadding: false, label: "City"},
  {id: "postcode", align: true, disablePadding: false, label: "Postcode"},
  {id: "buildings_count", align: true, disablePadding: false, label: "Buildings"},
  {id: "floors_count", align: true, disablePadding: false, label: "Floors"},
  {id: "rooms_count", align: true, disablePadding: false, label: "Rooms"},
  {id: "actions", align: true, disablePadding: false, numeric: 'right', label: "Actions"}
];
const SiteTable = () => {

  const [site_open, setSiteOpen] = useState(false);
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const [loader, setLoader] = useState(false);
  const {width} = useSelector(({settings}) => settings);
  const History = useHistory();

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [tabledata, setTabledata] = useState([]);
  const [sites, setSites] = useState([]);
  const [site_id, setSiteId] = useState(null);
  const [customername_rows, setCustomerNames] = useState([]);
  const [customer_id, setCustomerName] = useState(null);
  const [site_name, setSiteName] = useState("");
  const [address, setAddress] = useState("");
  const [site_city, setSiteCity] = useState("");
  const [site_postcode, setSitePostcode] = useState("");
  const [site_contact_name, setSiteContactName] = useState("");
  const [out_contact, setOutContacct] = useState("");
  const [any_site, setAnySite] = useState("");
  const [hours_access, setHoursAccess] = useState("");
  const [site_comment, setSiteComment] = useState("");
  const [goback, setGoback] = useState(false)

  useEffect(() => {
    init_Data();
  }, []);

  const init_Data = () => {
    $http.get(base_url + "site/siteList")
    .then((response) => {
      setTabledata(response.data.sites);
      setSites(response.data.sites);
      setLoader(true);
    })
    .catch((error) => {
      setGoback(true)
    })

  }

  const createSortHandler = property => event => {
    handleRequestSort(event, property);
  };

  const handleRequestSort = (event, property) => {
    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }

    
    sort_order === "desc"
      ? sites.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : sites.sort((a, b) => (a[property] < b[property] ? -1 : 1));

    
    setOrder(sort_order);
    setOrderBy(property);
  };

  const handleClick = (id) => {
    History.push({
      pathname: "/app/sites/mysite/Info",
      state: {id: id}
    })
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };
  const onSelectSite = (data, index) => {
    if (index === 0) {
      History.push({
        pathname: "/app/sites/mysite/Info",
        state: {id: data}
      })
    } else if (index === 1) {
      SiteModalOpen(data);
    } else if (index === 2) {
      const frmData = new FormData();
      frmData.append('id', data);
      $http.post(base_url + "site/deleteSite", frmData)
      .then((response) => {
        if (response.data.status === "success") {
          init_Data();
          NotificationManager.info("You removed selected site");
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 1000, () => {
            });
        }
        
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
      })

      if (!navigator.onLine) {
        caches.open('SirvezApp').then(function(cache) {
          cache.match('/api/site/siteList')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              responsedata.sites = responsedata.sites.filter((item) => {return item.id !== data})
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/site/siteList', jsonResponse.clone())
              .then(function(){
                init_Data()
              })
          })
        })
      }
    }
  }

  const SearchSite = (data) => {
    setSites(
      tabledata.filter(function (item) {
        return (item.site_name.toLowerCase().includes(data.toLowerCase()));
      })
    );
  }
  const SiteModalOpen = (data) => {
    setSiteId(data);
    $http.get(base_url + "site/getSiteInfo", {params : {id: data}})
    .then((response) => {
        setCustomerNames(response.data.customers);
        if (data) {
          setCustomerName(response.data.site.company_id);
          setSiteName(response.data.site.site_name);
          setAddress(response.data.site.address);
          setSiteCity(response.data.site.city);
          setSitePostcode(response.data.site.postcode);
          setSiteContactName(response.data.site.contact_name);
          setOutContacct(response.data.site.contact_number);
          setAnySite(response.data.site.site_instructions);
          setHoursAccess(response.data.site.access_hour);
          setSiteComment(response.data.site.comment);
        }
        setSiteOpen(true);
    })
    .catch((error) => {
      setGoback(true)
    })

  }
  const SiteModalClose = () => {
    setSiteOpen(false);
  }
  const SiteModalSave = () => {
    SiteModalClose();
    let offline_id = site_id ? site_id : JSON.parse(localStorage.getItem('user')).id + '_' + new Date().getTime()
      const frmData = new FormData();
      frmData.append('id', offline_id);
      frmData.append('customer_id', customer_id);
      frmData.append('site_name', site_name);
      frmData.append('address', address);
      frmData.append('city', site_city);
      frmData.append('postcode', site_postcode);
      frmData.append('contact_name', site_contact_name);
      frmData.append('contact_number', out_contact);
      frmData.append('site_instructions', any_site);
      frmData.append('access_hour', hours_access);
      frmData.append('comment', site_comment);

      $http.post(base_url + "site/updateSite", frmData)
      .then((response) => {
          if (response.data.status === "success") {
              NotificationManager.success('Updated site!', "Success!", 1000, () => {
              });
              if(navigator.onLine)
                init_Data();
          } else if (response.data.status === "error") {
              NotificationManager.error(response.data.msg, "Error!", 1000, () => {
              });
          }
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
      })

      if (!navigator.onLine) {
        caches.open('SirvezApp').then(function(cache) {
          var modify_data = {
            id : offline_id,
            customer_id : customer_id,
            site_name : site_name,
            address : address,
            city : site_city,
            postcode : site_postcode,
            contact_name: site_contact_name,
            contact_number: out_contact,
            site_instructions: any_site,
            access_hour: hours_access,
            comment: site_comment,
            buildings_count:0,
            floors_count:0,
            rooms_count :0,
          }
          cache.match('/api/site/getSiteInfo')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              responsedata.site= modify_data
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/site/getSiteInfo?id='+offline_id, jsonResponse.clone())
             
          })
          
          cache.match('/api/site/siteInfo?id='+offline_id)
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) 
              {
                responsedata = {
                  site:modify_data,
                  status:'success',
                  buildings:[]
                }
                const jsonResponse = new Response(JSON.stringify(responsedata), {
                  headers: {'content-type': 'application/json'}
                })
                cache.put('/api/site/siteInfo?id='+offline_id, jsonResponse.clone())
              }
              else{
                responsedata.site= modify_data
                const jsonResponse = new Response(JSON.stringify(responsedata), {
                  headers: {'content-type': 'application/json'}
                })
                cache.put('/api/site/siteInfo?id='+offline_id, jsonResponse.clone())
              }
          })        

          cache.match('/api/site/siteList')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              let item = responsedata.sites.find((item) => {return item.id === offline_id})
              if (item) {
                item.id = offline_id
                item.customer_id = customer_id
                item.site_name = site_name
                item.city = site_city
                item.postcode = site_postcode
                item.contact_name= site_contact_name
                item.contact_number= out_contact
                item.site_instructions= any_site
                item.access_hour= hours_access
                item.comment= site_comment
                
              } else {

                  responsedata.sites.unshift(modify_data)
              }
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/site/siteList', jsonResponse.clone())
              .then(function(){
                init_Data()
              })
          })
        })
      }
  }

  return (
    
        <div>
          <Toolbar className="table-header">
            <div className="title">
              <Typography variant="h6">Sites</Typography>
            </div>
            <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
                <SearchBox placeholder="Search ..."
                  onChange={(event) => SearchSite(event.target.value)}
                />
            </div>
            <div className="actions">
              <Tooltip title="New Site">
                <IconButton aria-label="New Site" onClick={() => SiteModalOpen(null)}>
                    <PlaylistPlayIcon/>
                </IconButton>
              </Tooltip>
            </div>
          </Toolbar>
          {
            loader ? (
              <div className="flex-auto">
                <div className="table-responsive-material">
                  <Table className="">
                    <TableHead>
                      <TableRow>
                        {columnData.map(column => {
                          return (
                            <TableCell
                              key={column.id}
                              align={column.numeric}
                            >
                              <Tooltip
                                title="Sort"
                                placement={column.numeric ? "bottom-end" : "bottom-start"}
                                enterDelay={300}
                              >
                                  <TableSortLabel
                                  active={orderBy === column.id}
                                  direction={order}
                                  onClick={createSortHandler(column.id)}
                                >
                                  {column.label}
                                </TableSortLabel>
                              </Tooltip>
                            </TableCell>
                          );
                        })}
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {
                        sites.length > 0 ?
                      sites.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((n) => 
                          <TableRow
                            hover
                            key={n.id}
                            /* onClick={event => handleClick(event, n.id)} */
                            //onKeyDown={event => handleKeyDown(event, n.id)}
                            tabIndex={-1}
                          >
                            <TableCell onClick={() => handleClick(n.id)}>{n.id}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.site_name}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.city}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.postcode}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.buildings_count}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.floors_count}</TableCell>
                            <TableCell onClick={() => handleClick(n.id)}>{n.rooms_count}</TableCell>
                            <TableCell className="text-right">
                              <CardEditMenu key={n.id} selectedData={n.id} 
                                onSelectRow={onSelectSite}/>
                            </TableCell>
                          </TableRow>
                        )
                        :
                        <tr>
                            <td className="text-danger" colSpan="8">
                                <IntlMessages id="table.noData"/>
                            </td>
                        </tr>
                      }
                    </TableBody>
                    <TableFooter>
                      <TableRow>
                        <TablePagination
                          count={sites.length}
                          rowsPerPage={rowsPerPage}
                          page={page}
                          onChangePage={handleChangePage}
                          onChangeRowsPerPage={handleChangeRowsPerPage}
                        />
                      </TableRow>
                    </TableFooter>
                  </Table>
                </div>
                <NotificationContainer/>
              </div>
            ):(
              <div className="loader-view"
                      style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                  <CircularProgress/>
              </div>        
            )
          }
          <Modal key="site-save" className="modal-box" toggle={SiteModalClose} isOpen={site_open}>
            <ModalHeader>{site_id ? 'Modify Site':'Add Site'}
                <IconButton className="text-grey"
                            onClick={SiteModalClose}>
                    <CloseIcon/>
                </IconButton>
            </ModalHeader>
            <div className="modal-box-content">
                <div className="row">
                    <div className="col-md-6 col-12">
                        <TextField
                            id="custemername"
                            select
                            label="Customer Name"
                            value={customer_id}
                            onChange={(event)=>setCustomerName(event.target.value)}
                            SelectProps={{}}
                            helperText="Please select Customer Name"
                            margin="normal"
                            fullWidth
                        >
                            {customername_rows.map(option => (
                                <MenuItem key={option.id} value={option.id}>
                                    {option.name}
                                </MenuItem>
                            ))}
                        </TextField>
                    </div>
                    
                    <div className="col-md-6 col-12">
                        <TextField 
                            id="site_name"
                            label="Site"
                            value={site_name}
                            onChange={(event) => setSiteName(event.target.value)}
                            margin="normal"
                            helperText="Please Enter Site"
                            fullWidth
                            />
                    </div>
                   
                    <div className="col-md-6 col-12">
                            <TextField 
                                id="address"
                                label="Address"
                                value={address}
                                onChange={(event)=>setAddress(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Address"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="site_city"
                                label="City"
                                value={site_city}
                                onChange={(event)=>setSiteCity(event.target.value)}
                                margin="normal"
                                helperText="Please Enter City"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="site_postcode"
                                label="Postcode"
                                value={site_postcode}
                                onChange={(event)=>setSitePostcode(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Postcode"
                                fullWidth
                            />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="site_contact_name"
                                label="Site Contact Name"
                                value={site_contact_name}
                                onChange={(event)=>setSiteContactName(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Site Contact Name"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="out_contact"
                                label="Out of ours contact number"
                                value={out_contact}
                                onChange={(event)=>setOutContacct(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Out of ours contact number"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="any_site"
                                label="Any site instructions"
                                value={any_site}
                                onChange={(event)=>setAnySite(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Any site instructions"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="hours_access"
                                label="Hours of access"
                                value={hours_access}
                                onChange={(event)=>setHoursAccess(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Hours of access"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-12 col-12">
                            <TextField
                                id="site_comment"
                                label="Comments"
                                multiline
                                rowsMax="4"
                                value={site_comment}
                                onChange={(event) => setSiteComment(event.target.value)}
                                margin="normal"
                                fullWidth
                            />
                        </div>
                  
                </div>
            </div>
            <div className="modal-box-footer d-flex flex-row jr-featured-content-right">
                <Button onClick={SiteModalClose} color="secondary" variant="contained" className="jr-btn text-white">
                Cancel
                </Button>
                <Button onClick={SiteModalSave} color="primary" variant="contained" className="jr-btn text-white">
                Save
                </Button>
            </div>
          </Modal>
          <SweetAlert show={goback}
              warning
              confirmBtnText="Go Back"
              confirmBtnBsStyle="danger"
              cancelBtnBsStyle="default"
              title="Warning!"
              onConfirm={() => History.go(-1)}
          >                
          Data not cached, so you can not view this page.
          </SweetAlert>

        </div>
        
  );
};

export default SiteTable;
